body{
    margin: 0%;
    box-sizing: border-box;
}

.container{
    border-bottom: 5px solid black;
   
    width:  100vw;
    margin: auto;
    background: #2c4864;  /* fallback for old browsers */


    border-radius: 20px;
}

.square{
    margin: auto;
    margin-top: 5px;

width: 60vw;
text-align: center;
background-color: #1FAA59;
border: 3px solid black;
border-radius: 20px;

}

.square span{
    
    display: block;
}
.square button{
    text-align: center;
}

.square button{
    background-color: blue;
    color: white;
    text-align: center;
    font-size: 15px;
    border-radius: 15px;
    border: 2px solid black;
}